@use 'postolny/variables';

/* Numerali */

#wrap_num {
  padding: 20px 0;
  box-sizing: border-box;
}
#risultato {
  display: inline-block;
  svg {
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
  }
  span {
    display: inline-block;
    vertical-align: middle;
    padding-left: 3px;
  }
}
#block-result {
  font-size: 14px;
  margin-bottom: 20px;
}
#next_num {
  margin-left: 3px;
}
#text_num {
  font-size: 4em;
  line-height: normal;
  color: variables.$default-shade;
  text-shadow: 3px 4px 3px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  word-break: break-all;
}
#an_num {
  border: 1px solid #ddd;
  box-sizing: border-box;
  text-indent: 8px;
  width: 100%;
  font-weight: bold;
  font-size: 2em;
  color: variables.$default-color;
  margin: 30px 0 10px;
  padding: 5px 0;
  transition: all ease-out 0.2s;
  &:focus {
    outline: none;
    border-color: variables.$default-tint;
  }
}
#suggerimento {
  display: none;
  transform: rotate(180deg);
  text-align: right;
}

/* Radio buttons */

.domanda {
  background: #efede7;
  margin-bottom: 1em;
  padding: 1em;
  p {
    margin-bottom: 10px;
  }
  label {
    padding: 0.2em 0;
  }
  label.correct {
    background: #8dc692;
    color: variables.$white;
  }
}
.radio {
  display: block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: left;

  + .radio {
    margin-top: 5px;
  }

  input {
    display: none;

    + span {
      display: inline-block;
      position: relative;
      padding-left: 25px;
      line-height: normal;

      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 0px;
        left: 0px;
        border-radius: 50%;
        margin-right: 5px;
        width: 20px;
        height: 20px;
        border: 1px solid variables.$grey-3;
        background: variables.$white;
        line-height: normal;
      }

      &:after {
        content: "";
        display: block;
        width: 14px;
        height: 14px;
        background: #222;
        position: absolute;
        border-radius: 50%;
        top: 4px;
        left: 4px;
        opacity: 0;
        transform: scale(0, 0);
        transition: all 0.2s cubic-bezier(0.64, 0.57, 0.67, 1.53);
      }
    }

    &:checked + span:after {
      opacity: 1;
      transform: scale(1, 1);
    }
  }
}
.quiz-msg {
  margin-top: 1em;
  padding: 0.5em;
}
.quiz-msg.correct {
  background: #e8ffc6;
}
.quiz-msg.incorrect {
  background: #ffe5cc;
}
.risultati {
  font-size: 14px;
  margin: 10px 0;
}
.quiz-submit {
  display: inline-block;
  cursor: pointer;
  color: variables.$white;
  padding: 5px 10px;
  background-color: variables.$default-shade;
}
.score {
  display: inline-block;
  padding: 5px;
}

/* Accento */

#accento {
  margin-left: 0;
  margin-bottom: 0;
  padding-left: 0;
  li {
    display: inline-block;
    list-style-type: none;
  }
  input {
    outline: none;
    border: 1px solid #3a3a3a;
    margin: 0 4px 0 2px;
    padding: 0;
    width: 20px;
    height: 20px;
    text-align: center;
    font-weight: bold;
    transition: all ease-out 0.2s;
    background: variables.$white;
    &:invalid {
      border-color: coral;
      background: coral;
      color: variables.$white;
    }
    &:valid {
      color: #8dc692;
    }
  }
}
#acc_dacapo {
  display: inline-block;
  font-size: 14px;
  cursor: pointer;
  color: variables.$white;
  margin: 10px 0;
  padding: 5px 10px;
  background-color: variables.$default-shade;
}

/* Completare */

.questions input[type="text"] {
  display: inline-block;
  font: inherit !important;
  width: 100px;
  height: 20px;
  border: 1px solid variables.$grey-3;
  border-width: 0px 0px 1px 0px;
  padding: 0px 5px;
  margin-top: 10px;
  outline: none;
  background-color: variables.$white;
}
.question {
  margin-bottom: 10px;
}
.corretto {
  background: #8dc692 !important;
  color: variables.$white !important;
}
.errato {
  background: coral !important;
  color: variables.$white !important;
}
.block-result {
  font-size: 14px;
  margin: 10px 0;
}
.mostrami,
#btn-mese,
#btn-giorno {
  display: inline-block;
  cursor: pointer;
  color: variables.$white;
  padding: 5px 10px;
  background-color: variables.$default-shade;
}
.result {
  display: inline-block;
  padding: 5px;
}

/* Frasario */

#fraseWrap {
  * {
    line-height: 1.3 !important;
  }
}

#frase,
#traduzione {
  font-size: 18px;
}

#traduzione {
  display: none;
}

#frasario-icon,
#playButtonRandom,
.random-icon,
.view-icon,
.close-icon {
  cursor: pointer;
}

#frasario-icon:hover {
  animation: pulse 1s linear infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

#frasario-icon-container {
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}

.frasarioModalWrapper {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: variables.$default-color;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  overflow: auto;
}

.frasarioModal {
  position: relative;
  margin: 10px;
  background-color: variables.$white;
  border-radius: 5px;
  max-width: 700px;
  max-height: 95%;
  overflow-y: auto;
  z-index: 1000;
}

.frasarioModalHeader {
  position: sticky;
  height: 30px;
  top: 0;
  z-index: 1001;
  display: flex;
  align-items: center;
  color: variables.$white;
  background-color: variables.$blue;

  span {
    &:first-child {
      margin-left: 3px;
    }

    &:last-child {
      margin-right: 3px;
    }
  }
}

#frasarioIcons {
  width: 100%;
  display: flex;
  justify-content: space-between;

  span {
    display: flex;
    align-items: center;
  }
}

#frasarioModalContent {
  padding: 15px 20px;

  h3 {
    font-size: 24px;
    line-height: 1.2;
    margin: 1rem 0;
  }
}

#fraseWrap {
  img {
    float: right;
    display: block;
    max-width: 100%;
    height: auto;
    padding: 10px 0 10px 10px;
  }
}
@media (max-width: 768px) {
  #fraseWrap {
    img {
      float: none;
      margin: 10px auto;
      padding: 0;
    }
  }
  #frasario-icon-container {
    bottom: 10px;
    right: 10px;
  }
}

.frasarioModalWrapper.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.frasarioModal {
  animation: modalFadeIn 0.3s forwards;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.random-icon {
  animation: rotateOnce 1s linear;
}

@keyframes rotateOnce {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.running {
  animation-play-state: running !important;
}

.paused {
  animation-play-state: paused !important;
}

#tocWrap {
	display: none;
	padding: 10px;
	background: #f9f9f9;
	border: 1px solid variables.$grey-3;
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}
	li {
		cursor: pointer;
		padding: 5px 10px;
		border-bottom: 1px solid #ddd;
		line-height: normal;
		&:hover {
			background: variables.$grey-3;
		}
	}
}

.toc-icon {
	fill: variables.$default-color;
	cursor: pointer;
}

#frasarioIcons span.disabled {
  pointer-events: none;
  opacity: 0.5;
}

/* Mese, giorno */

#wrap_mg {
  margin: 15px 0;
}

#wrap-result {
  font-size: 14px;
}

#risultato-mese,
#risultato-giorno {
  display: inline-block;
  svg {
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
  }
  span {
    display: inline-block;
    vertical-align: middle;
    padding-left: 3px;
  }
}

.evid {
  color: #00bf65;
  font-weight: bold;
}

#search-mese,
#search-giorno {
  box-sizing: border-box;
  font: inherit !important;
  width: 100px;
  border: 1px solid #ddd;
  margin: 10px 0;
  padding: 5px;
  transition: all ease-out 0.2s;
  &:focus {
    outline: none;
    border-color: variables.$default-tint;
  }
}

.quiz-submit,
.mostrami,
#btn-mese,
#btn-giorno {
  user-select: none;
  -webkit-user-select: none;
}

@media only screen and (max-width: 479px) {
  #wrap_mg label {
    display: block;
  }
  #questione:last-child {
    margin-top: 15px;
  }
  #risultato-mese,
  #risultato-giorno {
    display: block;
    margin-top: 8px;
    padding-left: 0;
  }
}
